.property-details{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
    gap: 20px;
    padding: 20px;
}
.carousel-container img{
    width: 100%;
    border-radius: 20px;
    height: 450px;
    object-fit: contain;
}
/* .property-details-container{
    background-color: #f0f2f5;
} */
.slick-dots {
    position: static;
}
.slick-dots li {
    width: auto;
}
.slick-dots img{
    width: 80px;
    height: 50px;
    object-fit: cover;
    cursor: pointer;
    border-radius: 5px;
}
.property-description h2{
    color: rgba(6, 36, 64, 255);
}
.special-info{

    background-color: rgba(6, 36, 64, 255);
    padding: 20px;
    border-radius: 20px;
    margin: 10px 0;
    color: white;
    font-weight: 500;

}
.info-item{
    gap: 10px;
    display: flex;
    align-items: center;
    margin: 5px 0;

}
#price h2, #price svg{
    color: rgba(52, 223, 129, 255);
}
.info-items{
    display: flex;
    gap: 10px;
}
.property-detail-accesory{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
}
.property-detail{
    list-style-type: none;
}
.property-accessory{
    list-style: none;
    margin-bottom: 5px;
}

.property-accessory li{
    margin-bottom: 10px;
}

.property-accessory label{
    display: flex;
    align-items: center;
    font-size: large;
    gap: 5px;
}
.property-detail label{
    display: flex;
    align-items: center;
    font-size: large;
    gap: 5px;
}
.accessory-flex{
    display: flex;
    justify-content: space-between;
 
}

@media (max-width: 576px) {

    .property-details{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        gap: 20px;
        padding: 20px;
    }
}